/**
 * ã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã®ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—ã‚’ç®¡ç†ã™ã‚‹ã‚¯ãƒ©ã‚¹
 * ãƒ¡ãƒ¢ãƒªãƒªãƒ¼ã‚¯ã‚’é˜²ããŸã‚ã€ã™ã¹ã¦ã®ã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã‚’è¿½è·¡ã—ã€é©åˆ‡ã«å‰Šé™¤ã™ã‚‹
 */
export class EventCleanupManager {
  private eventListeners: Map<string, {
    target: EventTarget
    type: string
    listener: EventListener
    options?: AddEventListenerOptions
  }[]> = new Map()
  
  private phaserListeners: Map<string, {
    emitter: Phaser.Events.EventEmitter
    event: string | symbol
    fn: Function
    context?: any
  }[]> = new Map()
  
  /**
   * DOMã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã‚’è¿½åŠ ï¼ˆè‡ªå‹•è¿½è·¡ï¼‰
   */
  addEventListener(
    id: string,
    target: EventTarget,
    type: string,
    listener: EventListener,
    options?: AddEventListenerOptions
  ): void {
    // ãƒªã‚¹ãƒŠãƒ¼ã‚’è¿½åŠ 
    target.addEventListener(type, listener, options)
    
    // è¿½è·¡ãƒªã‚¹ãƒˆã«è¿½åŠ 
    if (!this.eventListeners.has(id)) {
      this.eventListeners.set(id, [])
    }
    
    this.eventListeners.get(id)!.push({
      target,
      type,
      listener,
      options
    })
  }
  
  /**
   * Phaserã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã‚’è¿½åŠ ï¼ˆè‡ªå‹•è¿½è·¡ï¼‰
   */
  addPhaserListener(
    id: string,
    emitter: Phaser.Events.EventEmitter,
    event: string | symbol,
    fn: Function,
    context?: any
  ): void {
    // ãƒªã‚¹ãƒŠãƒ¼ã‚’è¿½åŠ 
    emitter.on(event, fn, context)
    
    // è¿½è·¡ãƒªã‚¹ãƒˆã«è¿½åŠ 
    if (!this.phaserListeners.has(id)) {
      this.phaserListeners.set(id, [])
    }
    
    this.phaserListeners.get(id)!.push({
      emitter,
      event,
      fn,
      context
    })
  }
  
  /**
   * ç‰¹å®šã®IDã«é–¢é€£ã™ã‚‹ã™ã¹ã¦ã®ã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã‚’å‰Šé™¤
   */
  removeAllListeners(id: string): void {
    // DOMã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã®å‰Šé™¤
    const domListeners = this.eventListeners.get(id)
    if (domListeners) {
      domListeners.forEach(({ target, type, listener, options }) => {
        try {
          target.removeEventListener(type, listener, options)
        } catch (e) {
          console.warn(`Failed to remove DOM listener: ${e}`)
        }
      })
      this.eventListeners.delete(id)
    }
    
    // Phaserã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã®å‰Šé™¤
    const phaserListeners = this.phaserListeners.get(id)
    if (phaserListeners) {
      phaserListeners.forEach(({ emitter, event, fn, context }) => {
        try {
          emitter.off(event, fn, context)
        } catch (e) {
          console.warn(`Failed to remove Phaser listener: ${e}`)
        }
      })
      this.phaserListeners.delete(id)
    }
  }
  
  /**
   * ã™ã¹ã¦ã®ã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã‚’å‰Šé™¤
   */
  removeAll(): void {
    // ã™ã¹ã¦ã®IDã®ãƒªã‚¹ãƒŠãƒ¼ã‚’å‰Šé™¤
    const allIds = [
      ...Array.from(this.eventListeners.keys()),
      ...Array.from(this.phaserListeners.keys())
    ]
    
    allIds.forEach(id => {
      this.removeAllListeners(id)
    })
  }
  
  /**
   * ãƒ¡ãƒ¢ãƒªä½¿ç”¨çŠ¶æ³ã®ãƒ¬ãƒãƒ¼ãƒˆ
   */
  getMemoryReport(): {
    domListenerCount: number
    phaserListenerCount: number
    ids: string[]
  } {
    let domCount = 0
    let phaserCount = 0
    
    this.eventListeners.forEach(listeners => {
      domCount += listeners.length
    })
    
    this.phaserListeners.forEach(listeners => {
      phaserCount += listeners.length
    })
    
    return {
      domListenerCount: domCount,
      phaserListenerCount: phaserCount,
      ids: [
        ...Array.from(this.eventListeners.keys()),
        ...Array.from(this.phaserListeners.keys())
      ]
    }
  }
  
  /**
   * ãƒ‡ãƒãƒƒã‚°ç”¨: ã‚¢ã‚¯ãƒ†ã‚£ãƒ–ãªãƒªã‚¹ãƒŠãƒ¼ã‚’ã‚³ãƒ³ã‚½ãƒ¼ãƒ«ã«å‡ºåŠ›
   */
  debugPrint(): void {
    const report = this.getMemoryReport()
    console.group('Event Cleanup Manager Report')
    console.log(`DOM Listeners: ${report.domListenerCount}`)
    console.log(`Phaser Listeners: ${report.phaserListenerCount}`)
    console.log(`Active IDs: ${report.ids.join(', ')}`)
    console.groupEnd()
  }
}